<template><section><h1>PolylineVolumeGraphics</h1>
<p><code v-pre>vc-graphics-polyline-volume</code> 组件用于加载多段线柱体。需要作为 <code v-pre>vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>添加多段线柱体到场景</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :polylineVolume.sync="polylineVolume1">
          <vc-graphics-polyline-volume ref="polylineVolume1" :positions="positions1" :shape="shape1" :material="material1"></vc-graphics-polyline-volume>
        </vc-entity>
        <vc-entity :polylineVolume.sync="polylineVolume2">
          <vc-graphics-polyline-volume :positions="positions2" :shape="shape2" :material="material2" :outline="true" :outlineColor="outlineColor2" :cornerType="cornerType2" ref="polylineVolume2"></vc-graphics-polyline-volume>
        </vc-entity>
        <vc-entity :polylineVolume.sync="polylineVolume3">
          <vc-graphics-polyline-volume :positions="positions3" :shape="shape3" :material="material3" :cornerType="cornerType3" ref="polylineVolume3"></vc-graphics-polyline-volume>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
