          { <span class="hljs-attr">lng</span>: <span class="hljs-number">-90.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">36.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">100000.0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">-94.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">36.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">0.0</span> }
