          { <span class="hljs-attr">x</span>: <span class="hljs-number">50000</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">-50000</span> },
          { <span class="hljs-attr">x</span>: <span class="hljs-number">-50000</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">50000</span> },
          { <span class="hljs-attr">x</span>: <span class="hljs-number">-50000</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">50000</span> }
        ],
        <span class="hljs-attr">material2</span>: <span class="hljs-literal">undefined</span>,
        <span class="hljs-attr">cornerType2</span>: <span class="hljs-number">0</span>,
        <span class="hljs-attr">outlineColor2</span>: <span class="hljs-string">'BLACK'</span>,
